<template>
  <el-menu
    :router="true"
    :default-active="$route.path"
    class="el-menu-vertical-demo"
    @open="handleOpen"
    @close="handleClose"
    :collapse="isCollapse"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
  <h3>{{isCollapse ?'后台':'后台管理系统'}}</h3>
    <el-menu-item @click="clickmenu(item)" v-for="item in noChildren" :key="item.name" :index="item.path === '/' ?item.path+'home':item.path">
      <i :class="`el-icon-${item.icon}`"></i>
      <span slot="title">
        {{item.label}}
      </span>
    </el-menu-item>
    <el-submenu v-for="item in hasChildren" :key="item.label" index="item.label">
      <template slot="title">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">
          {{item.label}}
        </span>
      </template>
      <el-menu-item-group v-for="subItem in item.children" :key="subItem.path"> 
        <el-menu-item @click="clickmenu(subItem)" :index="subItem.path">{{subItem.label}}</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>

<style lang="less">
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-menu{
  height: 100%;
  h3{
    color: #f1f1f1;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    line-height: 48px;
  }
}
</style>

<script>
import Cookie from 'js-cookie'
export default {
  name: "CommonAside",
  data() {
    return {

    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    clickmenu(value){
      // if(this.$route.path != value.path && !(this.$route.path === 'home' && (value.path === '/'))){
        this.$router.push(value.path)

      // }
      this.$store.commit('selectMenu',value)
    },
  },
  computed: {
    noChildren(){
      return this.menuData.filter(item => !item.children)
    },
      hasChildren(){
      return this.menuData.filter(item => item.children)
    },
     menuData(){
      // 判断当前数据，如果state中没有，就去缓存中获取
      console.log(this.$store.state.menu);
      return JSON.parse(Cookie.get('menu')) || this.$store.state.menu
    },
    isCollapse(){
       return this.$store.state.isCollapse
    } 
  },
};
</script>